/*ГЛОБАЛЬНЫЕ СТИЛИ*/
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
}
a {
  text-decoration: none;
  color: #000000;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/*БЛОКИ*/
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 1200px) {
  .page-body {
    background-color: #F2F2F2;
  }
}
@media (min-width: 1200px) {
  .page-body__inner {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
    background-color: #FFFFFF;
  }
}
.button {
  display: inline-block;
  vertical-align: middle;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #81B3D2;
  border: none;
}
.button--search-hotel {
  width: 100%;
  padding: 15px 20px 17px 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .button--search-hotel {
    width: 458px;
    padding: 15px 20px 17px 20px;
  }
}
@media (min-width: 768px) {
  .button--search-hotel {
    width: 458px;
    padding: 15px 20px 17px 20px;
  }
}
.button--send-feedback {
  width: 100%;
  padding: 19px 49px 13px 49px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .button--send-feedback {
    width: 50%;
    padding: 18px 60px 13px 49px;
  }
}
@media (min-width: 1200px) {
  .button--send-feedback {
    width: 480px;
    padding: 18px 60px 13px 60px;
  }
}
.button--failure {
  width: 64%;
  padding: 18px 19px 14px 20px;
  box-sizing: border-box;
  font-size: 18px;
}
@media (min-width: 768px) {
  .button--failure {
    width: 272px;
    font-size: 21px;
  }
}
.button--success {
  width: 100%;
  padding: 18px 20px 14px 20px;
  box-sizing: border-box;
  font-size: 18px;
}
@media (min-width: 768px) {
  .button--success {
