.main-navigation {
  position: relative;
}
.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 20px;
  display: block;
  width: 23px;
  height: 22px;
  margin-top: 17px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation__toggle:hover {
  opacity: 0.6;
}
.main-navigation__toggle:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
  .main-navigation--nojs .main-navigation__list {
    display: block;
  }
  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
}
.main-navigation--opened .main-navigation__toggle {
  width: 22px;
  height: 22px;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 27px;
  height: 4px;
  background-color: #FFFFFF;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 auto;
  }
}
.main-navigation__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-navigation__list::after {
    display: none;
  }
}
.main-navigation__item {
  padding-top: 10px;
  padding-bottom: 6px;
  text-align: center;
  background-color: #81B3D2;
}
@media (min-width: 768px) {
