  }
}
.main-navigation__item:not(:first-child) {
  box-shadow: inset 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 2px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-navigation__item:not(:first-child) {
    box-shadow: none;
  }
}
@media (min-width: 768px) {
  .main-navigation__item:nth-child(2),
  .main-navigation__item:nth-child(4n+2) {
    margin-right: 20%;
  }
}
@media (min-width: 768px) {
  .main-navigation__item:nth-child(3) {
    padding-left: 5px;
    box-sizing: border-box;
  }
}
.main-navigation__item:not(.main-navigation__item--current):hover {
  background-color: #669EC0;
}
@media (min-width: 768px) {
  .main-navigation__item:not(.main-navigation__item--current):hover {
    background-color: transparent;
  }
}
.main-navigation__item:not(.main-navigation__item--current):active {
  background-color: #5496BD;
}
@media (min-width: 768px) {
  .main-navigation__item:not(.main-navigation__item--current):active {
    background-color: transparent;
  }
}
.main-navigation__item:not(.main-navigation__item--current):active .main-navigation__link {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-navigation__item:not(.main-navigation__item--current):active .main-navigation__link {
    opacity: 1;
  }
}
.main-navigation__link {
